html, body{
  height: 100%;
}
section#main{
  margin: 0px;
  padding: 0px;
  height: 100%;
  background-color: #1B1F21;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.card-main{
  background-color: #353A40;
  width: auto;
  border-radius: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 8% 0px;
  padding: 8vh;
}

.header{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 80%;
}

.header h2{
  font-size: xx-large;
  color: #c7d1d4;
  font-weight: bold;
}

/* .header h4{
  font-size: larger;
  color: #4375D9;
} */

.formInputs{
  display: flex;
  align-items: center;
  flex-direction: row;
}

.formInputs label{
  color: #AFB9BC;
  font-weight: bold;
}

.formInputs .form-group{
  margin-right: 20px;
}

 .form-group label{
  color: #AFB9BC;
}

.formInputs .btn{
  margin-top: 10px;
}

.header img{
  width: 80px;
}

.resultado{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  font-weight: bold;
  color: #fff;
  background-color:#818080;
}

.resultado .reload{
  margin-left: 20px;
  
}

.card-main button{
  background-color: #495159 ;
  color: #fff;
}

.card-main button:hover{
  background-color: #616d79!important;
}

.card-main button:hover{
  background-color: #5983d8;
  color: #fff;
}

.footer{
  width: 100%;
  background-color: #353A40;
  color: #fff;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

